import { useState } from 'react';
import PropTypes from 'prop-types';
import { Navigate } from 'react-router-dom';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import Box from '@mui/material/Box';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Divider from '@mui/material/Divider';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import UpdateMemoryIcon from './UpdateMemoryIcon';
import DeleteMemoryIcon from './DeleteMemoryIcon';
import SimpleSnackbar from '../../components/simple_snackbar';
import MemoryActions from './MemoryActions';
import api from '../Api';

async function deleteMemory(bucket, authURL, memoryID, memoryDeleteCallback) {
  try {
    const deleteResults = await api.deleteMemory(bucket, authURL, memoryID);
    memoryDeleteCallback(deleteResults);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function MemoryContentStructure({
  header, subheader, photoLayout, storyLayout, bucket, memoryID, authURL, handleEditClick, preview,
}) {
  const [snack, setSnack] = useState(null);
  const [snackMessage, setSnackMessage] = useState(null);
  const [snackError, setSnackError] = useState(null);
  let className;
  const theme = useTheme();
  const lgdown = useMediaQuery(theme.breakpoints.down('lg'));
  if (lgdown) className = '';
  else className = 'vertical-scroll';

  const handleSnackMessage = (msg, error) => {
    setSnackMessage(msg);
    setSnackError(error);
  };

  const memoryDeleteCallback = (open) => {
    if (open.status === 'ok') {
      handleSnackMessage('Memory deleted successfully', false);
    } else {
      handleSnackMessage('Error while deleting memory', true);
    }
    setSnack(true);
  };

  const handleSnackBarClose = () => {
    setSnack(false);
    setSnackMessage(null);
    setSnackError(null);
  };

  const snackBar = (
    <SimpleSnackbar
      snackBarOpen={snack}
      message={snackMessage}
      onClose={handleSnackBarClose}
      error={snackError}
    />
  );

  const actions = !preview ? (
    <MemoryActions
      confirmMessage="Do you want to delete this memory?"
      snackBar={snackBar}
      handleDeleteObject={() => {
        deleteMemory(bucket, authURL, memoryID, memoryDeleteCallback);
      }}
      deleteItem={<DeleteMemoryIcon />}
      updateItem={<UpdateMemoryIcon />}
      handleEditClick={handleEditClick}
    />
  ) : <MoreVertIcon />;

  return (snack === false) ? (
    <Navigate to="/memories" replace />
  ) : (
    <Card
      sx={{
        boxShadow: 0,
        bgcolor: 'button.main',
        p: 1,
      }}
    >
      <CardHeader
        title={header}
        subheader={subheader}
        action={actions}
      />
      <Divider />
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-start',
          flexDirection: {
            lg: 'row',
            xs: 'column',
          },
          height: {
            lg: '72vh',
          },
          my: 1,
        }}
      >
        <Box
          sx={{
            width: {
              lg: '70%',
              xs: '100%',
            },
            overflowY: 'scroll',
            borderRadius: 5,
          }}
          className={className}
        >
          {photoLayout}
        </Box>
        <Box
          sx={{
            width: { lg: '30%', sx: '100%' },
            overflowY: 'scroll',
            mt: {
              lg: 0,
              xs: 2,
            },
            p: {
              lg: 1,
            },
            px: 1,
          }}
          className={className}
        >
          {storyLayout}
        </Box>
      </Box>
    </Card>
  );
}

MemoryContentStructure.propTypes = {
  header: PropTypes.oneOfType([PropTypes.string, PropTypes.element]).isRequired,
  subheader: PropTypes.oneOfType([PropTypes.string, PropTypes.element]).isRequired,
  photoLayout: PropTypes.element.isRequired,
  storyLayout: PropTypes.element.isRequired,
  memoryID: PropTypes.string.isRequired,
  bucket: PropTypes.string.isRequired,
  authURL: PropTypes.string.isRequired,
  handleEditClick: PropTypes.func.isRequired,
  preview: PropTypes.bool.isRequired,
};
