import PropTypes from 'prop-types';
import Dialog from '@mui/material/Dialog';
import CardHeader from '@mui/material/CardHeader';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import IconButton from '@mui/material/IconButton';

import MemoryImageSlide from './MemoryImageSlide';
import RemovePhotoIcon from './RemovePhotoIcon';

function NextPic({ handleClick }) {
  return (
    <IconButton
      edge="start"
      color="primary"
      aria-label="close"
      onClick={handleClick}
      sx={{
        position: 'absolute', top: '50%', right: 0, transform: 'translate(0%, -50%)',
      }}
    >
      <KeyboardArrowRight sx={{ color: 'button.main', fontSize: '4.5rem' }} />
    </IconButton>
  );
}

function PreviousPic({ handleClick }) {
  return (
    <IconButton
      edge="start"
      color="primary"
      aria-label="close"
      onClick={handleClick}
      sx={{
        position: 'absolute', top: '50%', left: 0, transform: 'translate(0%, -50%)',
      }}
    >
      <KeyboardArrowLeft sx={{ color: 'button.main', fontSize: '4.5rem' }} />
    </IconButton>
  );
}

export default function MemoryCarousel({
  open, title, date, images, index, handleNext, handleBack, handleClose, handleChange,
}) {
  return (
    <Dialog
      open={open}
      fullScreen
    >
      <MemoryImageSlide
        images={images}
        ind={index}
        handleNext={handleNext}
        handleStepChange={handleChange}
      />
      <CardHeader
        title={title}
        subheader={date}
        sx={{
          position: 'absolute',
          top: 10,
          left: 10,
          bgcolor: 'button.main',
          borderRadius: 3,
          padding: 1,
        }}
      />
      <PreviousPic handleClick={handleBack} />
      <NextPic handleClick={handleNext} />
      <RemovePhotoIcon size="large" handleClick={handleClose} />
    </Dialog>
  );
}

NextPic.propTypes = {
  handleClick: PropTypes.func.isRequired,
};

PreviousPic.propTypes = {
  handleClick: PropTypes.func.isRequired,
};

MemoryCarousel.defaultProps = {
  index: 0,
};

MemoryCarousel.propTypes = {
  open: PropTypes.bool.isRequired,
  title: PropTypes.string.isRequired,
  date: PropTypes.string.isRequired,
  index: PropTypes.number,
  handleNext: PropTypes.func.isRequired,
  handleBack: PropTypes.func.isRequired,
  handleClose: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  images: PropTypes.arrayOf(PropTypes.shape({
    src: PropTypes.string,
    width: PropTypes.number,
    height: PropTypes.number,
  })).isRequired,
};
