import { useState } from 'react';
import PropTypes from 'prop-types';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Confirm from '../../components/confirmation';

export default function MemoryActions({
  deleteItem, updateItem, snackBar, confirmMessage, handleDeleteObject, handleEditClick,
}) {
  const [confirm, setConfirm] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const handleDeleteClick = () => {
    setConfirm(true);
    handleClose();
  };

  const handleEdit = () => {
    handleEditClick();
    handleClose();
  };

  return (
    <div>
      <MoreVertIcon onClick={handleClick} />
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
        sx={{ borderRadius: '10px' }}
      >
        <MenuItem onClick={handleEdit}>{updateItem}</MenuItem>
        <MenuItem onClick={handleDeleteClick}>{deleteItem}</MenuItem>
      </Menu>
      <Confirm
        title="Delete Memory"
        confirm={confirm}
        handleCallback={handleDeleteObject}
        handleCloseCallback={() => setConfirm(false)}
        message={confirmMessage}
      />
      {snackBar}
    </div>
  );
}

MemoryActions.propTypes = {
  deleteItem: PropTypes.node.isRequired,
  updateItem: PropTypes.node.isRequired,
  confirmMessage: PropTypes.string.isRequired,
  snackBar: PropTypes.node.isRequired,
  handleDeleteObject: PropTypes.func.isRequired,
  handleEditClick: PropTypes.func.isRequired,
};
