import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import DoneTwoToneIcon from '@mui/icons-material/DoneTwoTone';
import ClearIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
import useTitle from '../../hooks/title';

export default function MemoryTitle({ editable, text, handleTitleUpdate }) {
  const [title, setTitle, handleTitleChange] = useTitle(text);
  const [edit, setEdit] = useState(editable);
  const [startEdit, setStartEdit] = useState(false);

  useEffect(() => {
    setEdit(editable);
    setTitle(text);
    setStartEdit(false);
  }, [editable]);

  if (!edit && !startEdit) {
    return <Typography variant="h6">{title}</Typography>;
  }

  if (edit && !startEdit) {
    return (
      <Stack direction="row" sx={{ alignItems: 'center' }} spacing={2}>
        <Typography variant="h6">{title}</Typography>
        <EditIcon fontSize="medium" onClick={() => setStartEdit(true)} />
      </Stack>
    );
  }

  if (edit && startEdit) {
    return (
      <Stack direction="row" sx={{ alignItems: 'flex-end', justifyContent: 'flex-start' }} spacing={2}>
        <TextField
          id="standard-basic"
          size="small"
          label="Title"
          onChange={handleTitleChange}
          variant="standard"
          defaultValue={title}
          sx={{ maxWidth: '200px' }}
        />
        <DoneTwoToneIcon
          fontSize="medium"
          onClick={() => {
            handleTitleUpdate(title);
            setStartEdit(false);
          }}
        />
        <ClearIcon fontSize="medium" onClick={() => { setTitle(text); setStartEdit(false); }} />
      </Stack>
    );
  }
}

MemoryTitle.propTypes = {
  editable: PropTypes.bool.isRequired,
  text: PropTypes.string.isRequired,
  handleTitleUpdate: PropTypes.func.isRequired,
};
