import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import Dialog from '@mui/material/Dialog';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import DialogTitle from '@mui/material/DialogTitle';

export default function Confirm({
  confirm, handleCallback, handleCloseCallback, title, message,
}) {
  const [open, setOpen] = useState(confirm);
  useEffect(() => setOpen(confirm), [confirm]);

  const handleClose = () => {
    setOpen(false);
    handleCloseCallback();
  };

  const handleSubmit = () => {
    handleClose();
    handleCallback();
  };

  return (
    <Dialog open={open} sx={{ backgroundColor: 'faded.black' }}>
      <DialogTitle>{title}</DialogTitle>
      <Box sx={{ p: 2 }}>
        <Typography sx={{ pb: 2 }}>{message}</Typography>
        <Stack spacing={2} direction="row">
          <Button variant="contained" onClick={handleClose}>Cancel</Button>
          <Button variant="outlined" onClick={handleSubmit}>OK</Button>
        </Stack>
      </Box>
    </Dialog>
  );
}

Confirm.propTypes = {
  confirm: PropTypes.bool.isRequired,
  title: PropTypes.string.isRequired,
  message: PropTypes.string.isRequired,
  handleCallback: PropTypes.func.isRequired,
  handleCloseCallback: PropTypes.func.isRequired,
};
