import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import PhotoAlbum from 'react-photo-album';
import Radio from '@mui/material/Radio';
import FormControlLabel from '@mui/material/FormControlLabel';
import DeleteIcon from '@mui/icons-material/Delete';
import IconButton from '@mui/material/IconButton';
import AddPhotoIcon from '@mui/icons-material/AddPhotoAlternate';
import Button from '@mui/material/Button';
import CloseIcon from '@mui/icons-material/Close';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import { alpha } from '@mui/material/styles';

function OptionsButton({
  children, onClick, name, updateOp,
}) {
  return (
    <Button
      id={name}
      variant="contained"
      component="label"
      onClick={onClick}
      sx={{
        color: (updateOp === name) ? 'button.main' : 'muted.main',
        borderRadius: 5,
        backgroundColor: (updateOp === name) ? 'primary.main' : 'button.main',
        padding: '6px 8px',
        '&:hover': {
          color: (updateOp === name) ? 'button.main' : 'muted.main',
          backgroundColor: (updateOp === name) ? 'primary.main' : 'button.main',
        },
      }}
    >
      {children}
    </Button>
  );
}

function AddPhotos({ handleChange, onClick, updateOp }) {
  return (
    <OptionsButton onClick={onClick} name="add" updateOp={updateOp}>
      <input type="file" hidden multiple accept="image/*" onChange={handleChange} />
      <AddPhotoIcon sx={{ fontSize: '1.5rem' }} />
      Add
    </OptionsButton>
  );
}

function MemoryPhotoAlbum({ images, handlePhotoClick, renderPhoto }) {
  const [arrangement, setArrangement] = useState(null);
  useEffect(() => {
    let twidth = 0;
    let theight = 0;
    images.forEach(({ height, width }) => {
      if (height > width) theight += 1;
      else twidth += 1;
    });
    if (twidth > theight) setArrangement('rows');
    else setArrangement('columns');
  }, [images]);

  return renderPhoto ? (
    <PhotoAlbum
      layout={arrangement}
      photos={images}
      onClick={handlePhotoClick}
      spacing={2}
      renderPhoto={renderPhoto}
    />
  ) : (
    <PhotoAlbum
      layout={arrangement}
      photos={images}
      onClick={handlePhotoClick}
      spacing={2}
    />
  );
}

export default function MemoryImages({
  editable, images, handlePhotoClick, handleThumbnailUpdate, deleteImage, addImage,
}) {
  const [edit, setEdit] = useState(editable);
  const [updateOp, setUpdateOp] = useState(null);
  const [thumbnail, setThumbnail] = useState(null);

  const handleOpUpdate = (op) => {
    if (updateOp === op) {
      setUpdateOp(null);
    } else {
      setUpdateOp(op);
    }
  };

  useEffect(() => {
    setEdit(editable);
    setUpdateOp(null);
  }, [editable]);

  const handleThumbnailChange = (name) => {
    setThumbnail(name);
    const thumbnailImage = images.find((image) => image.alt === name);
    handleThumbnailUpdate(thumbnailImage);
  };

  const handleImageDelete = (name) => {
    const deleted = images.find((image) => image.alt === name);
    deleteImage(deleted);
  };

  const renderPhotosWithDeleteIcon = ({
    layout,
    imageProps: { alt, style, ...restImageProps },
  }) => (
    <Button component="div">
      <img
        alt={alt}
          // eslint-disable-next-line react/jsx-props-no-spreading
        style={{ ...style, width: layout.width, padding: 0 }}
          // eslint-disable-next-line react/jsx-props-no-spreading
        {...restImageProps}
      />
      <IconButton
        size="medium"
        sx={{
          color: 'black',
          bgcolor: alpha('#fff', 0.4),
          position: 'absolute',
          top: 10,
          right: 12,
        }}
        onClick={() => handleImageDelete(alt)}
      >
        <CloseIcon size="medium" />
      </IconButton>
    </Button>
  );

  const renderPhotosWithRadioButton = ({
    layout,
    imageProps: { alt, style, ...restImageProps },
  }) => (
    <Button component="div">
      <img
        alt={alt}
          // eslint-disable-next-line react/jsx-props-no-spreading
        style={{ ...style, width: layout.width, padding: 0 }}
          // eslint-disable-next-line react/jsx-props-no-spreading
        {...restImageProps}
      />
      <FormControlLabel
        control={<Radio />}
        label=""
        checked={alt === thumbnail}
        onChange={() => handleThumbnailChange(alt)}
        sx={{
          pr: 1,
          position: 'absolute',
          bgcolor: alpha('#fff', 0.6),
          color: 'black',
          borderRadius: '0px 5px 5px 0px',
          borderColor: 'secondary.main',
          borderLeft: 0,
          bottom: 20,
          left: 15,
        }}
      />
    </Button>
  );

  const OptionsRow = (
    <Stack direction="row" spacing={2}>
      <AddPhotos handleChange={addImage} onClick={() => handleOpUpdate('add')} updateOp={updateOp} />
      <OptionsButton onClick={() => handleOpUpdate('delete')} name="delete" updateOp={updateOp}>
        <DeleteIcon sx={{ fontSize: '1.5rem' }} />
        Delete
      </OptionsButton>
      <OptionsButton onClick={() => handleOpUpdate('thumbnail')} name="thumbnail" updateOp={updateOp}>Set Thumbnail</OptionsButton>
    </Stack>
  );

  const updatePanel = (
    <Stack direction="row">
      <Typography variant="h6" color="muted.main">
        Images:
        {images.length}
      </Typography>
    </Stack>
  );

  if (!edit) {
    return (
      <MemoryPhotoAlbum images={images} handlePhotoClick={handlePhotoClick} />
    );
  }

  if (edit && updateOp === 'delete') {
    return (
      <Stack direction="column" spacing={2} sx={{ p: 2 }}>
        {updatePanel}
        {OptionsRow}
        <MemoryPhotoAlbum
          images={images}
          handlePhotoClick={handlePhotoClick}
          renderPhoto={renderPhotosWithDeleteIcon}
        />
      </Stack>
    );
  }

  if (edit && updateOp === 'thumbnail') {
    return (
      <Stack direction="column" spacing={2} sx={{ p: 2 }}>
        {updatePanel}
        {OptionsRow}
        <MemoryPhotoAlbum
          images={images}
          handlePhotoClick={handlePhotoClick}
          renderPhoto={renderPhotosWithRadioButton}
        />
      </Stack>
    );
  }

  if (edit && updateOp === 'add') {
    return (
      <Stack direction="column" spacing={2} sx={{ p: 2 }}>
        {updatePanel}
        {OptionsRow}
        <MemoryPhotoAlbum
          images={images}
          handlePhotoClick={handlePhotoClick}
        />
      </Stack>
    );
  }

  if (edit && !updateOp) {
    return (
      <Stack direction="column" spacing={2} sx={{ p: 2 }}>
        {updatePanel}
        {OptionsRow}
        <MemoryPhotoAlbum images={images} handlePhotoClick={handlePhotoClick} />
      </Stack>
    );
  }
}

MemoryImages.propTypes = {
  editable: PropTypes.bool.isRequired,
  images: PropTypes.arrayOf(
    PropTypes.shape({
      src: PropTypes.string,
      width: PropTypes.number,
      height: PropTypes.number,
    }),
  ).isRequired,
  handlePhotoClick: PropTypes.func.isRequired,
  handleThumbnailUpdate: PropTypes.func.isRequired,
  deleteImage: PropTypes.func.isRequired,
  addImage: PropTypes.func.isRequired,
};

MemoryPhotoAlbum.propTypes = {
  images: PropTypes.arrayOf(
    PropTypes.shape({
      src: PropTypes.string,
      width: PropTypes.number,
      height: PropTypes.number,
    }),
  ).isRequired,
  handlePhotoClick: PropTypes.func.isRequired,
  renderPhoto: PropTypes.func,
};

MemoryPhotoAlbum.defaultProps = {
  renderPhoto: null,
};

AddPhotos.propTypes = {
  handleChange: PropTypes.func.isRequired,
  onClick: PropTypes.func.isRequired,
  updateOp: PropTypes.string.isRequired,
};

OptionsButton.defaultProps = {
  onClick: () => {},
};

OptionsButton.propTypes = {
  children: PropTypes.node.isRequired,
  onClick: PropTypes.func,
  name: PropTypes.string.isRequired,
  updateOp: PropTypes.string.isRequired,
};
