import { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import Stack from '@mui/material/Stack';
import DoneTwoToneIcon from '@mui/icons-material/DoneTwoTone';
import ClearIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
import Divider from '@mui/material/Divider';
import Typography from '@mui/material/Typography';
import TextareaAutosize from '@mui/material/TextareaAutosize';
import Box from '@mui/material/Box';
import useStory from '../../hooks/story';
import './memory.css';

export default function MemoryStory({ editable, prevStory, handleStoryUpdate }) {
  const [edit, setEdit] = useState(editable);
  const [startEdit, setStartEdit] = useState(false);
  const [story, setStory, handleStoryChange] = useStory(prevStory);
  const storyRef = useRef();

  useEffect(() => {
    setEdit(editable);
    setStory(prevStory);
    setStartEdit(false);
  }, [editable, prevStory]);

  if (!edit && !startEdit) {
    return (
      <Typography style={{ whiteSpace: 'pre-wrap' }}>{story}</Typography>
    );
  }

  if (edit && !startEdit) {
    return (
      <Stack direction="column" spacing={2}>
        <Stack direction="row" spacing={2} justifyContent="flex-end">
          <Typography sx={{ color: 'muted.main' }}>Edit Story</Typography>
          <EditIcon
            fontSize="medium"
            onClick={() => {
              setStartEdit(true);
            }}
          />
        </Stack>
        <Divider sx={{ width: 1 }} />
        <Typography style={{ whiteSpace: 'pre-wrap' }}>{story}</Typography>
      </Stack>
    );
  }

  if (edit && startEdit) {
    return (
      <Stack direction="column" spacing={2}>
        <Stack direction="row" spacing={2} justifyContent="flex-end">
          <Typography sx={{ color: 'muted.main' }}>Editing Story</Typography>
          <DoneTwoToneIcon
            fontSize="medium"
            onClick={() => {
              handleStoryUpdate(story);
              setStartEdit(false);
            }}
          />
          <ClearIcon
            fontSize="medium"
            onClick={() => {
              setStory(prevStory);
              setStartEdit(false);
            }}
          />
        </Stack>
        <Divider sx={{ width: 1 }} />
        <Box
          sx={{
            p: 2,
            m: 1,
            my: 2,
            border: 1,
            borderColor: 'primary.main',
            bgcolor: 'input.background',
            minHeight: '70vh',
            borderRadius: 5,
            width: '90%',
          }}
          onClick={() => storyRef.current.focus()}
        >
          <TextareaAutosize
            autoFocus
            aria-label="minimum height"
            placeholder="Tell your story..."
            value={story}
            onChange={handleStoryChange}
            style={{
              width: '100%',
              minHeight: '184px',
              maxHeight: '70vh',
              boxSizing: 'border-box',
              paddingTop: '5px',
              border: 0,
              overflow: 'auto',
              outline: 'none',
              resize: 'vertical',
              borderRadius: 5,
              '&:focus': {
                border: 0,
              },
            }}
            ref={storyRef}
          />

        </Box>
      </Stack>
    );
  }
}

MemoryStory.propTypes = {
  prevStory: PropTypes.string.isRequired,
  editable: PropTypes.bool.isRequired,
  handleStoryUpdate: PropTypes.func.isRequired,
};
