import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import DoneTwoToneIcon from '@mui/icons-material/DoneTwoTone';
import ClearIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
import DatePicker from '@mui/lab/DesktopDatePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import AdapterDateFns from '@mui/lab/AdapterDateFns';

import useDate from '../../hooks/date';

export default function MemoryDate({ editable, prevDate, handleDateUpdate }) {
  const [edit, setEdit] = useState(editable);
  const [startEdit, setStartEdit] = useState(false);
  const [date, setDate, handleDateChange] = useDate(prevDate);

  useEffect(() => {
    setEdit(editable);
    setDate(prevDate);
    setStartEdit(false);
  }, [editable, prevDate]);

  if (!edit && !startEdit) {
    return <Typography>{date}</Typography>;
  }

  if (edit && !startEdit) {
    return (
      <Stack direction="row" sx={{ alignItems: 'center' }} spacing={2}>
        <Typography>{date}</Typography>
        <EditIcon fontSize="medium" onClick={() => setStartEdit(true)} />
      </Stack>
    );
  }

  if (edit && startEdit) {
    return (
      <Stack direction="row" sx={{ alignItems: 'flex-end', justifyContent: 'flex-start', mt: 3 }} spacing={2}>
        <LocalizationProvider dateAdapter={AdapterDateFns}>
          <DatePicker
            label="Memory Date"
            value={date}
            onChange={handleDateChange}
            placeholder="dd/MM/yyyy"
            inputFormat="dd/MM/yyyy"
            maxDate={new Date()}
            renderInput={(props) => (
              <TextField
                readOnly
                required
          // eslint-disable-next-line react/jsx-props-no-spreading
                {...props}
              />
            )}
          />
        </LocalizationProvider>
        <DoneTwoToneIcon
          fontSize="medium"
          onClick={() => {
            handleDateUpdate(date);
            setStartEdit(false);
          }}
        />
        <ClearIcon fontSize="medium" onClick={() => { setDate(prevDate); setStartEdit(false); }} />
      </Stack>
    );
  }
}

MemoryDate.propTypes = {
  editable: PropTypes.bool.isRequired,
  prevDate: PropTypes.string.isRequired,
  handleDateUpdate: PropTypes.func.isRequired,
};
