import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Snackbar from '@mui/material/Snackbar';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Stack from '@mui/material/Stack';
import DoneIcon from '@mui/icons-material/Done';
import Typography from '@mui/material/Typography';

function SnackText({ message, error }) {
  if (error === true) {
    return (
      <Stack direction="row" spacing={2} justifyContents="space-between" alignItems="center">
        <CloseIcon fontSize="small" color="error" />
        <Typography>{message}</Typography>
      </Stack>
    );
  }
  return (
    <Stack direction="row" spacing={2} justifyContents="space-between" alignItems="center">
      <DoneIcon fontSize="small" color="success" />
      <Typography>{message}</Typography>
    </Stack>
  );
}

export default function SimpleSnackbar({
  snackBarOpen, message, onClose, error,
}) {
  const [open, setOpen] = useState(snackBarOpen);
  const [snackMessage, setSnackMessage] = useState(message);

  useEffect(() => setOpen(snackBarOpen), [snackBarOpen]);
  useEffect(() => setSnackMessage(message), [message]);

  const handleClose = () => {
    /*    if (reason === 'clickaway') { */
    /* return; */
    /* } */

    setOpen(false);
    setSnackMessage(null);
    onClose();
  };

  const action = (
    <IconButton
      size="small"
      aria-label="close"
      color="inherit"
      onClick={handleClose}
    >
      <CloseIcon fontSize="small" />
    </IconButton>
  );

  return (
    <Snackbar
      open={open}
      autoHideDuration={3000}
      onClose={handleClose}
      message={<SnackText message={snackMessage} error={error} />}
      action={action}
      anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
    />
  );
}

SimpleSnackbar.defaultProps = {
  error: false,
};

SimpleSnackbar.propTypes = {
  message: PropTypes.string.isRequired,
  snackBarOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  error: PropTypes.bool,
};

SnackText.propTypes = {
  message: PropTypes.string.isRequired,
  error: PropTypes.bool.isRequired,
};
